import { Request } from '@/request/axios';
import { reactive, ref } from 'vue'
import qs from 'qs'
const local = "/api"
//const local = ""

export interface FileList {
    fileName: string;
    path:string;
    type:string;//图片 img,视频: video,文件： file
    time:number;
    downloadPath:string,
    fileSize:number
}
export const fileLists: FileList[] = reactive([]);
//getFileList,downFile,uploadFile
export function getFileList(parameter: any) {
    return Request.axiosInstance({
        url: local + '/file/getFileList',
        method: 'post',
        data: parameter
    })
}
//下载文件
export function downFile(parameter: any) {
    return Request.axiosInstance({
        url: local + '/file/downFile',
        method: 'post',
        data: parameter
    })
}
//上传文件
export function uploadFile(parameter: any) {
    return Request.axiosInstance({
        url: local + '/file/uploadFile',
        method: 'post',
        data: parameter
    })
}
//保存配置
export function saveConfigRequest(parameter: any){
    return Request.axiosInstance({
        url: local + '/saveConfig',
        method: 'post',
        data: parameter
    })
}
//获取配置
export function getConfigRequest(parameter: any){
    return Request.axiosInstance({
        url: local + '/getConfig',
        method: 'post',
        data: parameter
    })
}